interface EducationEntryProps {
    date: string;
    degree: string;
    major: string;
}

const EducationEntry: FC<EducationEntryProps> = ({ date, degree, major }) => {
    const textColor = useColorModeValue("gray.700", "gray.200");
    const bulletColor = useColorModeValue("primary.500", "primary.300");


    return (
        <Box position="relative" mb={1}>
            <Box
                as="span"
                position="absolute"
                left="-8px"
                top="1px"
                w="0"
                h="0"
                borderTop="4px solid transparent"
                borderBottom="4px solid transparent"
                borderLeft={`7.5px solid ${bulletColor}`}
            />
            <Box pl={2}>
              <Text fontSize="16">
                    <Box pb="2" as="span" fontSize="18" fontWeight="700" color={bulletColor}>{degree} </Box>
                    <Box as="span" fontSize="15" fontWeight="700" color={textColor}> {date}</Box>
                </Text>
               
                <Text fontSize="16" fontWeight="500">{major}</Text>
                
            </Box>
        </Box>
    );
};


