export const Research: FC = () => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [progress, setProgress] = useState(10);
  const [isPaused, setIsPaused] = useState(false);

  const [selectedCard, setSelectedCard] = useState<CardItem | null>(null);
  const { isOpen, onOpen, onClose } = useDisclosure();

  const intervalRef = useRef<NodeJS.Timeout | null>(null);
  const duration = 6500;
  const tick = 20;
  const steps = duration / tick;

  useEffect(() => {
    if (!isPaused) {
      intervalRef.current = setInterval(() => {
        setProgress((prev) => {
          if (prev >= 100) {
            goToNext();
            return 10;
          }
          return prev + 100 / steps;
        });
      }, tick);
    }

    return () => {
      if (intervalRef.current) clearInterval(intervalRef.current);
    };
  }, [currentIndex, isPaused]);

  const goToPrevious = () => {
    setCurrentIndex((prev) => (prev === 0 ? cardData.length - 1 : prev - 1));
    setProgress(10);
  };

  const goToNext = () => {
    setCurrentIndex((prev) => (prev + 1) % cardData.length);
    setProgress(10);
  };

  const handleSelect = (index: number) => {
    setCurrentIndex(index);
    setProgress(10);
  };

  const handleCardClick = (card: CardItem) => {
    setSelectedCard(card);
    onOpen();
  };

  return (
    <Box w="100%" maxW="800px" mx="auto" textAlign="center">
      <Box position="relative" height="420px" overflow="hidden">
        {cardData.map((card, index) => {
          const isCurrent = index === currentIndex;
          const isLeft =
            index === (currentIndex - 1 + cardData.length) % cardData.length;
          const isRight = index === (currentIndex + 1) % cardData.length;

          const display = isCurrent || isLeft || isRight ? "block" : "none";
          const scale = isCurrent ? 1 : 0.9;
          const zIndex = isCurrent ? 2 : 1;
          const opacity = isCurrent ? 1 : 0.5;
          const offsetX = isLeft ? "-60%" : isRight ? "60%" : "0";

          return (
            <Box
              key={card.id}
              onClick={() => handleCardClick(card)}
              cursor="pointer"
              position="absolute"
              top="0"
              left="50%"
              transform={`translateX(-50%) translateX(${offsetX}) scale(${scale})`}
              transition="all 0.5s ease-in-out"
              zIndex={zIndex}
              opacity={opacity}
              display={display}
              w="100%"
              maxW="600px"
              
            >
              <Box
                borderWidth="1px"
                borderRadius="lg"
                overflow="hidden"
                boxShadow="lg"
                bg="white"            
                p={4}
              >
                <Box
                position="absolute"
                top="0"
                right="0"
                p={2}
                zIndex={3}
              >
                <ExIcon color="black" style={{ width: "24px", height: "24px" }} />
                
              </Box>
                <Center h="200px">
                  <Image
                    src={card.image}
                    alt={card.title}
                    maxH="100%"
                    maxW="100%"
                    objectFit="contain"
                    mt="1"
                  />
                </Center>
                {/* <Heading color="black" fontSize="xl" mt={4}>
                  {card.title}
                </Heading>
                <Text mt={2} color="gray.600">
                  {card.description}
                </Text> */}
                <Flex gap={1} justify="flex-start" align="flex-start" w="full">
                <Text
                  as="h2"
                  fontSize="m"
                  fontWeight="bold"
                  color="black"
                  noOfLines={2}
                  textAlign="left"
                  mt="3" 
                  mb="1"   // ‚Üê ÏÉÅÏúÑÏùò centerÎ•º ÎçÆÏñ¥ÏîÄ
                >
                  {card.paper}
                </Text>
              </Flex>
            
            <Flex gap={1} align="center">
                {/* {project.recent === "yes" && <Box as="span" bg="teal.500" color="white" px="2" py="0.5" borderRadius="full" fontSize="xs">New</Box>} */}
              
                {card.date && <Text fontSize="sm" color="gray.500" fontWeight="bold">{card.date}, </Text>}
                {card.conference && <Text fontSize="sm" color="gray.500" fontWeight="bold">{card.conference}</Text>}
                
              </Flex>

              
              {card?.tags && card.tags.length > 0 && (
              <Box mb={1}>
                <Tags_domestic tags={card.tags} size="xs" />
         
              </Box>
            )}
              
              </Box>
            </Box>
          );
        })}

        <Box
          as="button"
          onClick={goToPrevious}
          position="absolute"
          left="0"
          top="40%"
          transform="translateY(-50%)"
          zIndex={3}
          w="50px"
          h="80px"
          display="flex"
          alignItems="center"
          justifyContent="center"
          borderRadius="full"
          _hover={{ bg: "gray.100" }}
        >
          <PreviousIcon color="black" style={{ width: "40px", height: "40px" }} />
        </Box>

        <Box
          as="button"
          onClick={goToNext}
          position="absolute"
          right="0"
          top="40%"
          transform="translateY(-50%)"
          zIndex={3}
          w="50px"
          h="80px"
          display="flex"
          alignItems="center"
          justifyContent="center"
          borderRadius="full"
          _hover={{ bg: "gray.100" }}
        >
          <NextIcon color="black" style={{ width: "40px", height: "40px" }} />
        </Box>
      </Box>

      <HStack mt="-50" spacing={2} justify="center">
        {cardData.map((_, index) => (
          <Box
            key={index}
            w="80px"
            cursor="pointer"
            onClick={() => handleSelect(index)}
          >
            <Progress
              
              size="xs"
              colorScheme={index === currentIndex ? "blue" : "gray"}
              value={
                index === currentIndex ? progress : index < currentIndex ? 100 : 0
              }
              borderRadius="full"
              transition="all 0.2s"
            />
          </Box>
        ))}
      </HStack>

      {/* Î™®Îã¨ */}
      <Modal isOpen={isOpen} onClose={onClose} isCentered size="l">
      
  <ModalOverlay />
  <ModalContent
  w={{ base: "95vw", md: "60vw", lg: "50vw" }}
  maxW="none"
  display="flex"
  flexDirection="column"
  maxH="90vh" // Ï†ÑÏ≤¥ ÎÜíÏù¥ Ï†úÌïú
>
  <ModalHeader
    fontSize="20"
    borderBottom="1px solid"
    borderColor="gray.200"
    _dark={{ borderColor: "gray.600" }}
    px={6}
    py={4}
    position="sticky"
    top={0}
    bg={useColorModeValue("gray.50", "gray.800")}
    zIndex={1}
    
  >
    {selectedCard?.paper}
  </ModalHeader>

  <ModalCloseButton />

  <ModalBody
    px={6}
    pt={4}
    pb={2}
    overflowY="auto"
    flex="1"
  >
    <Text fontSize="16" fontWeight="700">{selectedCard?.conference}</Text>

    <Text fontSize="16" fontWeight="700" color="gray.500" mb={1}>
      {selectedCard?.date}
    </Text>

    {selectedCard?.tags && selectedCard.tags.length > 0 && (
      <Box mb={4}>
        <Tags tags={selectedCard.tags} />
      </Box>
    )}

    <Image
      src={selectedCard?.image}
      alt={selectedCard?.title}
      mb={6}
      maxH="300px"
      mx="auto"
      objectFit="contain"
    />

    <Text fontSize="md" fontWeight="bold" color="gray.700" mb={1} _dark={{ color: "gray.100" }}>
      abstract
    </Text>
    <Text fontSize="md" color="gray.800" _dark={{ color: "gray.100" }} mb={4}>
      {selectedCard?.abstract}
    </Text>
  </ModalBody>

  <ModalFooter  px={5} py={4} bg={useColorModeValue("gray.50", "gray.800")}>
    {selectedCard?.link && (
      <Button
        as="a"
        href={selectedCard.link}
        target="_blank"
        rel="noopener noreferrer"
        // colorScheme="teal"
        variant="outline"
        mr={3}
        px={1}
        py={0}
      >
        View Paper
      </Button>
    )}
    <Button px={3} onClick={onClose}>Close</Button>
  </ModalFooter>
</ModalContent>

</Modal>

    </Box>
  );
};
