// HighlightTag.tsx
import { Text, useColorModeValue } from "@chakra-ui/react";
import { FC, ReactNode } from "react";

export const HighlightTag: FC<{ children: ReactNode; color: string }> = ({ children, color }) => {
  const bg = useColorModeValue(`${color}.100`, `${color}.900`);
  const textColor = useColorModeValue(`${color}.800`, `${color}.50`);

  return (
    <Text
      as="span"
      px={0.5}
      py={0}
      bg={bg}
      color={textColor}
      fontSize="16" 
      fontWeight="semibold"
      whiteSpace="nowrap"
    >
      {children}
    </Text>
  );
};
